@import (less) "color.less";

.step-container {
    background-color: #F8F8F8;
    border-bottom: 0.0625em solid #ccc;
    margin-top: -0.125em;
}

.sticky {
    position: fixed;
    right: 0;
    left: 0;
    z-index: 1030;
    top: 3.375em;
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
    .sticky {
        .step-header-table {
            top: 0;
        }
    }
    .step-header-table {
        top: -7px;
        position: relative;
    }
}

.icomoon-ToDo_Stepper {
    display: none;
    color: @green;
    font-size: 1.084em;
    color: #cccccc;
    position: relative;
    top: -3px;
    left: -2px;
}

.icomoon-ToDo_Stepper2 {
    display: none !important;
    color: @green;
    font-size: 1.084em;
    position: relative;
    top: -3px;
    left: -2px;
}

.icomoon-Freccia_Stepper {
    display: none;
    font-size: 1.834em;
    color: @green;
    position: relative;
    top: -3px;
    left: -2px;
}

.icomoon-Spunta_Stepper {
    display: none;
    font-size: 1.834em;
    color: @green;
    position: relative;
    top: -3px;
    left: -2px;
}

.icomoon-Spunta_Stepper2 {
    display: none;
    font-size: 1.834em;
    color: @green;
    position: relative;
    top: -3px;
    left: -2px;
}

.cont-step-header {
    padding: 0 0 0 0 !important;
    //background-color: #eee;
    height: 3.25em !important;
}
.step-header-table {
    table-layout: fixed;
    width: 100%;
    margin-top: -0.750em;
}
.step-header-row {
    .tabName {
        font-size: 0.75em;
    };
    text-align: center;
    color: @gray;
    height: 3.125em;
    text-transform: uppercase;
    font-size: 0.750em;
    font-weight: bold;
    td {
        padding-top: 0.375em;
        vertical-align: top;
        position: relative;
        &:last-child{
            &::after{
                display: none;
            }
        }
        &::after{
            content: "";
            display: block;
            position: absolute;
            top: 1.8125em;
            width: 100%;
            height: 0.125em;
            left: 50%;
            border-top: 0em solid;
            border-bottom: .167em;
            margin-left: 15%;
            width: 69%;
            background-color: @light_green;
        }
    }
    .step-cerchio{
        position: relative;
        z-index: 1;
        margin: 1em auto 0.5em auto;
        display: block;
        width: 1.5625em;
        // width: 1.4em;
        height: 1.5625em;
        // height: 1.399em;
        border-radius: 100%;
        background-color: #f8f8f8;
        cursor: pointer;
        &.completed{
            //background-color: @light_green;
            &:hover {
                background-color: @white;
                //border: 0.0625em solid @light_green;
                .icomoon-Spunta_Stepper {
                    display: none !important;
                }
                .icomoon-Spunta_Stepper2 {
                    display: inline !important;
                }
                .icomoon-ToDo_Stepper {
                    display: none !important;
                }
            }
            .icomoon-Spunta_Stepper {
                display: inline !important;
            }
            .icomoon-ToDo_Stepper {
                display: none !important;
            }
        }
    }
    .step-corrente {
        color: @green;
        .step-cerchio{
            margin-top: .75em;
            width:1.5625em;
            height: 1.6825em;
            background-color: #f8f8f8;
            //border: 0.125em solid @light_green;
        }
        .icomoon-Freccia_Stepper {
            display: inline !important;
        }
        span {
            cursor: pointer;
            color: @green;
        }
    }
    .disabled-step {
        color: @green;
        .step-cerchio {
            cursor: default;
            width: 1.004em;
            height: 1.004em;
            margin-bottom: .71em;
            margin-top: 1.2em;
        }
        .tabName{
            opacity: 0.5;
            cursor: default;
        }
        .icomoon-ToDo_Stepper {
            display: inline !important;
            opacity: 0.5;
            color: @green;
        }
    }
    .step-completare {
        color:@green;
        .step-cerchio{
            width: 1.004em;
            height: 1.004em;
            margin-bottom: .71em;
            margin-top: 1.2em;
            //background-color: #eee;
            //border: 0.0625em solid @light_green;
            &:hover {
                background-color: @white;
                .icomoon-ToDo_Stepper2 {
                    display: inline !important;
                }
                .icomoon-Spunta_Stepper {
                    display: none !important;
                }
                .icomoon-ToDo_Stepper {
                    display: none !important;
                    color: @green;
                }
            }
            .icomoon-ToDo_Stepper {
                display: inline !important;
                color: @green;
            }
            .icomoon-Spunta_Stepper {
                display: none !important;
            }
            .icomoon-Spunta_Stepper2 {
                display: none !important;
            }
        }
        span {
            cursor: pointer;
        }
    }
    .completed-step{
        color: @green;
        &::after{
            background-image: none;
            background-color: @green;
            border-style: solid;
        }
        span {
            cursor: pointer;
        }
    }
    .step-completato{
        .step-cerchio {
            margin-top: .75em;
            margin-bottom: 0.6em;
        }
        color: @green;
        span {
            cursor: pointer;
        }
    }
}
.step-content {
  padding: 1.250em;
  text-align: left;
}

:host-context(.contrasto-on) {
    .step-container {
        background-color: @white;;
        border-bottom: 0.0625em solid @black;
    }
    
    .icomoon-ToDo_Stepper {
        color: @black;
    }
    
    .icomoon-ToDo_Stepper2 {
        color: @black;
    }
    
    .icomoon-Freccia_Stepper {
        color: @black;
    }
    
    .icomoon-Spunta_Stepper {
        color: @black;
    }
    
    .icomoon-Spunta_Stepper2 {
        color: @black;
    }
    
    .step-header-row {
        color: @white;
        td {
            &::after{
                background-color: @white;
            }
        }
        .step-cerchio{
            background-color: @white;
            &.completed{
                &:hover {
                    background-color: @white;
                }
            }
        }
        .step-corrente {
            color: @black;
            .step-cerchio{
                background-color: @white;
            }
            span {
                color: @black;
            }
        }
        .disabled-step {
            color: @black;
            .icomoon-ToDo_Stepper {
                color: @black;
            }
        }
        .step-completare {
            color:@black;
            .step-cerchio{
                &:hover {
                    background-color: @white;
                    .icomoon-ToDo_Stepper {
                        color: @black;
                    }
                }
                .icomoon-ToDo_Stepper {
                    color: @black;
                }
            }
        }
        .completed-step{
            color: @black;
            &::after{
                background-color: @black;
            }
        }
        .step-completato{
            color: @black;
        }
    }
}